* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
header {
  padding: 1rem 0;
  background-color: lightcoral;
  color: white;
  text-align: center;
  font-size: 2rem;
}
.list li {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 1rem 2rem;
  border-bottom: 1px solid rgb(137, 137, 137);
}
.title {
  font-size: 1.25rem;
}
.date {
  color: rgb(137, 137, 137);
}
.btn {
  cursor: pointer;
  border: none;
  background-color: transparent;
  font-size: 1.25rem;
}
.counter {
  font-size: 1.25rem;
  padding-left: 1rem;
}
.add-box {
  background-color: blanchedalmond;
  padding: 2rem;
  border-radius: 10px;
  max-width: 600px;
  width: 80%;
  margin: auto;
  margin-top: 2rem;
  position: relative;
}
.add-box input {
  display: block;
  background-color: rgba(188, 188, 188, 0.6);
  border: none;
  padding: 0.5rem 1rem;
  border-radius: 10px;
}
.add-box input:nth-child(1) {
  margin-bottom: 0.5rem;
}
.btnAdd {
  position: absolute;
  right: 0;
  bottom: 2rem;
  margin-right: 2rem;
  padding: 0.5rem 1rem;
  border: none;
  background-color: rgb(188, 188, 188);
  border-radius: 10px;
  color: white;
  cursor: pointer;
}
.btnAdd:hover {
  color: black;
  transition: 0.3s;
}
/* modal */
.modal {
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100vh;
  background-color: rgba(0, 0, 0, 0.7);
  display: flex;
  justify-content: center;
  align-items: center;
}
.con {
  background-color: white;
  padding: 2rem;
  border-radius: 10px;
  max-width: 500px;
  width: 50%;
  position: relative;
}
.modal h1 {
  text-align: center;
}
.modal p {
  padding: 1rem 0;
}
.close {
  position: absolute;
  top: 0;
  right: -40px;
}
